<template>
  <div>
    
    <CRow class="mb-3">
      <CCol>
        <h1>Semua Stimulasi</h1>
      </CCol>
      
    </CRow>
    
    <CRow>
      <CCol col="12" v-for="(stimulation,idx) in stimulations" :key="idx">
        <CCard>
          <!-- <CCardHeader>
            {{stimulation.judul}}
          </CCardHeader> -->
          <CCardBody>
            <div flex class="flex-row">
              <div inline-flex>
                <p>{{stimulation.judul}}</p>
              </div>
              <div inline-flex>
                <p>{{stimulation.video_embed}}</p>
              </div>
            </div>
          </CCardBody>
          <CCardFooter>
            <CRow>
              <!-- <CCol col="6">
                <CRow class="mx-0">
                  <Ccol class="px-0">
                    <div inline-block class="mr-2">
                      Replay {{stimulation.total_reply_forum}}
                    </div>
                  </Ccol>
                  <Ccol class="px-0">
                    <div inline-block class="mr-2">
                      View {{stimulation.total_view}}
                    </div>
                  </Ccol>
                </CRow>
              </CCol> -->
              <CCol>
                <CRow class="justify-content-end">
                  <CCol col="6" sm="4" md="2" xl="2" class="mb-3 mb-xl-0">
                    <CButton block variant="outline" color="primary" @click="goDetailStimulasi(stimulation.id)"> Detail</CButton>
                  </CCol>
                  <CCol col="6" sm="4" md="2" xl="2" class="mb-3 mb-xl-0">
                    <CButton block variant="outline" color="primary" @click="goEditStimulasi(stimulation.id)"><CIcon name="cil-pencil"/> Edit</CButton>
                  </CCol>
                </CRow>
              </CCol>
            </CRow>
            
          </CCardFooter>
        </CCard>
      </CCol>
    </CRow>
    
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'IndexStimulation',
  data(){
    return {
      stimulations: []
    }
  },
  methods: {
    setStimulation(data){
      this.stimulations = data;
    },
    goEditStimulasi(id){
      let stimulasiId = id;
      this.$router.push({ path: `/stimulasi/edit/${stimulasiId}` });
      localStorage.removeItem('stimulasiId');
      localStorage.setItem('stimulasiId', id);
    },
    goDetailStimulasi(id){
      let stimulasiId = id;
      this.$router.push({ path: `/stimulasi/detail/${stimulasiId}` });
      localStorage.removeItem('stimulasiId');
      localStorage.setItem('stimulasiId', id);
    }
  },
  created() {
    let body = {
      admin_user_id : localStorage.getItem('admin_id')
    };

    axios
      .post('http://103.151.215.74/api/admin/stimulasi/list', body, {
        headers: {
          Authorization: localStorage.getItem('token')
        }
      })
      .then(
        // (response) => this.setThread(response.data.json_data)
        (response) => {
          // console.log("Berhasil : ", response);
          this.setStimulation(response.data.json_data);
        }
        
      )
      // .catch(function (error) {
      //   // handle error
      //   // console.log("Gagal : ", error);
      // })
  },
}
</script>